* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  background-color: #23252C;
  color: white
}

.app-container{
  width: 50%;
  margin: 50px auto;   
  text-align: center;
  
}

.app{
  position: relative;
  width: 100%;
  max-width: 600px;
  min-width: 300px;
}

.info-div{
  background-color: #1A1B21; 
  display: inline-block;
  min-width: 300px;
  border-radius: 10px 10px 0 0;
}

.info-img {
width: 317px;
height: 317px;
border-radius: 10px 10px 0px 0px;
}

.info-name {
font-family: "Inter";
font-style: normal;
font-weight: 700;
font-size: 25px;
line-height: 30px;
text-align: center;
color: #ffffff;
}

.info-profession {
font-family: "Inter";
font-style: normal;
font-weight: 400;
font-size: 12.8px;
line-height: 15px;
text-align: center;
color: #f3bf99;
}

.btn-container{
  display: flex;
  align-items: center;
}
.email-btn {
margin: 17px;
width: 115px;
height: 34px;
background: #ffffff;
border: 1px solid #d1d5db;
box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
border-radius: 6px;
}

.linkedin-btn {
margin: 17px;
width: 115px;
height: 34px;
background: #5093e2;
border: 1px solid #5093e2;
box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
border-radius: 6px;
color: white;
}
.about-div{
  background-color: #1A1B21;
  display: inline-block;
  text-align: left;
  padding: 20px 10px;
  min-width: 317px;

}
.about-h3 {
padding-left: 36px;
font-family: "Inter";
font-style: normal;
font-weight: 700;
font-size: 16px;
line-height: 30px;
/* or 24px */
color: #f5f5f5;
}

.about-p {
padding-left: 36px;
width: 247px;
height: 61px;
font-family: "Inter";
font-style: normal;
font-weight: 400;
font-size: 10.24px;
line-height: 150%;
/* or 15px */
color: #dcdcdc;
}
.interest-div{
  background-color: #1A1B21;
  display: inline-block;
  text-align: left;
  padding: 20px 10px;
  min-width: 317px;
}
.interests-h3 {
padding-left: 36px;
width: 242.71px;
font-family: "Inter";
font-style: normal;
font-weight: 700;
font-size: 16px;
line-height: 150%;
color: #f5f5f5;
}

.interests-p {
padding-left: 36px;
width: 247px;
height: 61px;
left: 152px;
top: 675px;

font-family: "Inter";
font-style: normal;
font-weight: 400;
font-size: 10.24px;
line-height: 150%;

/* or 15px */

color: #dcdcdc;
}

.footer-div{
  background: #161619;
  display: inline-block;
  text-align: left;
  padding: 20px 10px;
  min-width: 317px;
  

}

